.addres-icon{
    margin-right: 0;
    text-align: center;
    padding: 0;
}

.footer{
    background-color: #e9ddce;
    padding: 3% 2% 1% 2% ;
}

@media (max-width: 992px) {
    .addres-container p{
        font-size: 80%;
    }

}

.presentation{
    background-color: rgb(238, 238, 238);
    padding-top: 6%;
    padding-bottom: 6%;
}

.carousel-item{
    margin: 0 auto;
    height: 40%;
    width: 50%;
    text-align: center;
    justify-content: center;
    
}

.carousel-caption{
    color:rgb(0, 0, 0);
    font-weight: bolder;
}
p{
    color:rgb(0, 0, 0)
}
.carousel-img{
    opacity: 0.5;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    border-radius: 4px;
}

.carousel-inner{
    display: flex;
    justify-content: center;
    text-align: center;
   
}


.carousel-text{
    font-weight: 400;
    font-style: italic;
}

@media (max-width: 992px) {
    .presentation{
        width:100%;
      
        justify-content: center;
        text-align: center;
    }
    .carousel-text{
    font-size: 80%;
    }
    .carousel-img{
        opacity: 0.7;
        max-width: 120%;
        min-height: 120%;
        margin: auto;
        border-radius: 4px;
       
    }
    .carousel-inner{
        justify-content: center;
        text-align: center;
       
    }
    .carousel-presentation{
        display: flex !important;
    }

 
}

.products{
    margin-top: 5%;
    margin-bottom: 5%;
}

.products-gallery-img{
    width: 100%;
}
.products-gallery-img:hover{
    border: 5px solid white;
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
}

.column-img{
    padding: 15px !important;
    margin: 0 !important;
}

.modal-content-gallery{
    padding: 0 !important;
    background: rgba(0,0,0,0.0) !important;
    border-width: 0;
    width: 80%;
    margin-left: 10%;
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 0;
    margin-left: auto;
    margin-right: auto;

}
.btn-close{
    color: white !important;
}
@media (max-width: 992px){
    .modal-content-gallery{
       width: 190%;
    }
}


@media (max-width: 992px) {
.carousel-indicators{
    visibility: hidden;
}
.carousel-gallery-buttons{
    visibility: hidden;
}
.carousel-inner{
    display: inline-block
   
}

}
